#review_wrap {
    position: relative;
    margin: 0 auto;
    margin-top: 30px;
}

#review_wrap .view_review {
    width: 100%;
    margin-right: 30px;
    border: 2px solid #dadada;
    border-radius: 10px;
    padding: 25px;
    background-color: #fafafa;
    text-align: left
}

#review_wrap .sec_item_title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid var(--sec-border-color);
}

#review_wrap .view_review_header {
    border-bottom: 2px solid #dadada;
    display: flex;
    align-items: center;
    gap: 30px;
    padding-bottom: 25px;
}

#review_wrap .view_review_header span:nth-child(1) {
    font-weight: bold;
    font-size: 3rem;
}

#review_wrap .view_review_header span:nth-child(2) {
    font-weight: bold;
}

#review_wrap .view_review_header .btn_highlight {
    width: 80px;
    height: 30px;
}

#review_wrap .view_review_list {
}

/* 리뷰 쓰는 칸 사이즈 */
textarea.review_text, textarea.review_modify_text {
    max-width: 500px;
    min-width: 500px;
    max-height: 110px;
    min-height: 110px;
    padding: 10px;
}

.review_write_modal {
    display: flex;
    flex-direction: column;
}

.write_bottom {
    display: flex;
    gap: 5px;
    align-items: end;
}

.write_bottom button {
    width: 50px;
    height: 30px;
}

.full_list {
    display: flex;
    flex-direction: column;
    gap: 5px;
    border-bottom: 2px dotted #dadada;
    padding: 25px;
}

.write_info {
    display: flex;
    gap: 8px;
    align-items: center;
}

.full_list .write_info span:nth-child(1) {
    font-weight: bold;
}

.full_list .write_info span:nth-child(2) {
    font-size: 12px;
    color: #8b8b8b;
}

.full_list .write_info span:nth-child(3) {
    font-size: 22px;
    color: var(--highlight);
}

.review_value {
    font-size: 17px;
    display: flex;
    gap: 5px;
}

.review_value span {
    width: 800px;
    word-break: break-all;
}

.review_value button {
    width: 50px;
}

.view_review_list .modify_bt, .view_review_list .delete_bt, .review_modify_modal button {
    width: 50px;
    font-size: 14px;
}

.review_modify_modal {
    margin-bottom: 10px;
}

.review_write_modal .star_wrap {
    width: 100%;
    justify-content: start;
    padding: 5px 0;
}

.review_modify_text {
    font-size: 16px;
    padding: 10px;
    margin: 10px 0;
}

.review_modify_modal {
    display: flex;
    flex-direction: column;
}

.review_modify_modal span {
    margin-right: 10px;
}

.review_modify_modal span:nth-child(1) {
    font-weight: bold;
}

.more_view_wrap {
    margin-top: 20px;
}

.modify_info {
    display: flex;
    padding: 10px 0 0;
}

.more_view_wrap {
    display: flex;
    gap: 10px;
}

.modify_btn {
    margin-left: 20px;
}

#review_wrap .more_view_wrap .more_btn, #review_wrap .more_view_wrap .hide_btn {
    border: none;
    background: none;
    border-radius: 0px;
    color: black;
    border-bottom: 1px solid #dadada;
}